<template>
  <!-- <p>{{ info }}</p> -->
  <el-dialog
    custom-class="color-dialog"
    v-model="state.dialogVisible"
    @close="handleClose"
    width="30%"
  >

    <!-- header -->
    <template #title>
      <!-- <span style="font-size: 20px; font-weight: bolder;"> -->
      <span>
         
      </span>
    </template>

    <!-- content -->
    <div class="d-flex justify-content-between">
      <el-select v-model="value" clearable placeholder="Select">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          id="item.value"
        >
        </el-option>
      </el-select>
      <div>
        <el-button @click="editColor"> </el-button>
      </div>
    </div>
    <!-- footer -->
  </el-dialog>
</template>

<script>
import { reactive, computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { ref } from 'vue'

export default {
  name: 'color-dialog',
  emits: ['edit-color'],
  // emits: ['create-birthday'],
  props: {
    open: {
      type: Boolean,
      default: false
    },
    // info: {
    //   type: Object,
    //   required: true,
    // }
  },
  setup(props, { emit }) {
    // const store = useStore()
    const router = useRouter()
    const state = reactive({
      dialogVisible: computed(() => props.open),
    })
    const value = ref('')

    const handleClose = function () {
      emit('closeColorDialog')
    }

    const options = [
  {
    value: '1',
    label: '()',
  },
  {
    value: '2',
    label: '',
  },
  {
    value: '3',
    label: '',
  },
  {
    value: '4',
    label: '',
  },
  {
    value: '5',
    label: '',
  },
]

  const editColor = function () {
      if ( ref(value).value != '') {
        // emit('edit-color', {
        //   color: ref(value),
        // })
        emit('edit-color', {
          jellyc: ref(value).value,
        })
        console.log(' ')
        console.log(ref(value).value)
        // console.log(props.info.cdate)
        localStorage.setItem('jelly', JSON.stringify(ref(value).value));
        setTimeout(() => {
                swal({
                  title: " ",
                  text: "  ..",
                  icon: "success",
                  button: "",
                });
              }, 500)

              router.go(router.currentRoute)
        handleClose()

      } else {
        setTimeout(() => {
                swal({
                  title: "  ",
                  text: " ",
                  icon: "error",
                  button: "",
                });
              }, 500)

              // router.go(router.currentRoute)
      }
    }

    return { state, handleClose, value,  options, editColor }
  }

}
</script>

<style>
  .color-dialog {
    width: 300px !important;
  }
</style>
