<template>
  <el-dialog custom-class="signup-dialog" v-model="state.dialogVisible" @close="handleClose" :destroy-on-close="true" :close-on-click-modal="false" :close-on-press-escape="false" top="50px" style="width:100%">
    <!-- header -->
    <template #title>
      <span>
           
      </span>
    </template>

    <!-- content -->
    <el-form :model="state.form" status-icon :rules="state.rules" ref="signupForm" :label-position="state.form.align">
      <el-form-item prop="id" label="" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.id" autocomplete="off" style="width: 70%" :disabled="state.form.isValidatedId" id="id-input" placeholder="ID"></el-input>
        <el-button size="small" style="float: right; margin-top:5px;" @click="checkDup" :disabled="state.form.isValidatedId"> </el-button>
        <!-- <el-button size="small" style="float: right; margin-top:5px;" @click="sendAuthEmail" :disabled="state.form.isValidatedAuth">  </el-button> -->

      </el-form-item>
      <el-form-item prop="authNumber" label="" ref="authForm" :label-width="state.formLabelWidth">
        <el-input id="auth-number" v-model="state.form.authNumber" autocomplete="off" style="width:70%" :disabled="state.form.isValidatedAuth" show-authNumber placeholder=""></el-input>
        <el-button size="small" style="float: right; margin-top:5px;" @click="clickAuthup" :disabled="state.form.isValidatedAuth"> </el-button>
      </el-form-item>
      <el-form-item prop="password" label="" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.password" autocomplete="off" show-password placeholder="Password" id="password"></el-input>
      </el-form-item>
      <el-form-item prop="passwordConfirm" label=" " :label-width="state.formLabelWidth">
        <el-input v-model="state.form.passwordConfirm" autocomplete="off" show-password placeholder="Password Confirmation" id="password-confirmation"></el-input>
      </el-form-item>
      <el-form-item prop="name" label="" :label-width="state.formLabelWidth" >
        <el-input v-model="state.form.name" autocomplete="off" placeholder="Name"></el-input>
      </el-form-item>
      <el-form-item prop="birthDate" label="" :label-width="state.formLabelWidth" >
        <el-date-picker
          style="width: 100%;"
          v-model="state.form.birthDate"
          type="date"
          value-format="YYYY-MM-DD"
          placeholder="Birthday"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item prop="nickname" label="" :label-width="state.formLabelWidth" >
        <el-input v-model="state.form.nickname" autocomplete="off" placeholder="Nickname"></el-input>
      </el-form-item>

    </el-form>

    <!-- footer -->
    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="clickSignup" v-loading.fullscreen="loading"></el-button>
      </span>
    </template>
  </el-dialog>
</template>
<style>
.signup-dialog {
  transform: translate(0, -10%) !important;
  width: 500px !important;
  height: 550px !important;
}
.signup-dialog .checkbox .el-form-item__content {
  float: left;
  margin-left: 3rem !important;
}
.signup-dialog .el-dialog__body {
  padding-bottom: 0px;
}
.signup-dialog .el-dialog__headerbtn {
  float: right;
}
.signup-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 300px;
  display: inline-block;
}
.signup-dialog .el-form-item {
  margin-bottom: 20px;
}
.signup-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.signup-dialog .el-input__suffix {
  display: none;
}
.signup-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.signup-dialog .dialog-footer .el-button {
  width: 120px;
}

.signup-dialog .el-checkbox a {
  color: #0d6efd;
  text-decoration: underline;
}
.signup-dialog .el-checkbox a:hover {
  background-color: rgba(141, 29, 216, 0.322);
}

</style>
<script>
import { reactive, computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default {
  name: 'signup-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    //      -  . <return>
    const signupForm = ref(null)
    const authForm = ref(null)
    const router = useRouter()
    const loading = ref(false)

    //    
    // (password)  (passwordConfirm)   
    const validatePass = (rule, value) => {
      if (value !== state.form.password) {
        return new Error("  .")
      } else {
        return true
      }
    }

    //    
    const validateAgree = (rule, value) => {
      if (!value) {
        return new Error(" .")
      } else {
        return true
      }
    }
    //   
    const validateAuth = (rule, value) => {
      if (!value) {
        return new Error("  .")
      } else {
        state.form.isPossibleAuth = true
        return true
      }
    }

    const validateId = (rule, value) => {
      console.log(value.length)
      if (!value || !value.trim()) {
        return new Error(" .")
      }// else if (!/^[a-zA-Z0-9]+$/.test(value)) {
       // return new Error("   ,    .")
      //} else if (value.length < 5 || value.length > 16) {
        //return new Error("  5 ~ 16  .")
      //}
      else {
        state.form.isPossibleId = true
        return true
      }
    }

    /*
      // Element UI Validator
      // rules    form      
      //
    */
    const state = reactive({
      form: {
        auth: {

        }, //     
        isPossibleId: false, // id    . true     .
        isValidatedId: false, //  id    . true   , false   id
        isValidatedAuth: false, //    
        agreement: false, //    
        id: '',
        password: '',
        authNumber:'',
        passwordConfirm: '',
        name: '',
        nickname: '',
        align: 'left',
        birthDate: '',
      },
      rules: {
        id: [
          { required: true, message: ' .', trigger: 'blur' },
          // { min: 5, max: 16, message: '  5 ~ 16  .', trigger: 'change' },
          { validator: validateId, trigger: 'blur'}
        ],
        password: [
          { required: true, message: ' .', trigger: 'blur' },
          { pattern: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{5,16}$/, message: '5 ~ 16 , ,   .'}
        ],
        authNumber:[
          { required: true, message: ' .', trigger: 'blur' },
          { validator: validateAuth, trigger: 'blur'}
        ],
        passwordConfirm: [
          { required: true, message: ' .', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' },
        ],
        name: [
          { required: true, message: ' .', trigger: 'blur' },
          // { min: 2, max: 10, message: '2 ~ 10  .', trigger: 'change' },
          { pattern: /^[a-zA-z-|-|-]{2,10}$/, message: '    . (,   )'}
        ],
        nickname: [
          { required: true, message: ' .', trigger: 'blur' },
          // { min: 2, max: 10, message: '2 ~ 10  .', trigger: 'change' },
          { pattern: /^[a-zA-z-|-|-\d]{2,10}$/, message: ', (, )  . (,   )'}
        ],
        birthDate: [
          { required: true, message: ' .', trigger: 'blur'}
        ],
        agreement: [
          { validator: validateAgree, message: ' .'},
        ]
      },
      dialogVisible: computed(() => props.open),
      isAvailable: computed(function () {
        return signupForm.value.validate((valid) => valid)
      }),
      isAvailableAuth: computed(function () {
        return authForm.value.validate((valnum) => valnum)
      }),
      formLabelWidth: '120px',
    })

    onMounted(() => {
      // console.log(loginForm.value)
    })
    const clickAuthup = function () {
      //    validate      ,  API    
      if (!state.form.isPossibleAuth) {
        swal({
          title: '  .',
          icon: 'error',
          button: ''
        })
        state.form.isPossibleAuth = false
      } else {
        if(state.form.authNumber==state.auth.str){
          console.log(" ");
          document.getElementById("password").focus()
          swal({
                  title: " ",
                  text: " .",
                  icon: "success",
                  button: "",
                });
          state.form.isValidatedAuth = true
        }
        //console.log("");
        // authForm.value.validate((valnum) => {
        //   if (valnum) {
        //     loading.value = TextTrackCue
        //     if(state.form.authNumber==state.auth.str){
        //       //   
        //       console.log(" ");
        //       state.form.isValidatedAuth = true;
        //       setTimeout(() => {
        //         loading.value = false
        //         swal({
        //           title: " ",
        //           text: " .",
        //           icon: "success",
        //           button: "",
        //         });
        //       }, 500)
        //     }


        //   } else {
        //     swal({
        //       title: " ",
        //       text: "  .",
        //       icon: "error",
        //       button: "",
        //     })
        //   }
        // });
      }
    }

    const clickSignup = function () {
      //    validate      ,  API    
      if (!state.form.isValidatedId) {
        swal({
          title: '   .',
          icon: 'error',
          button: ''
        })
      }else if(!state.form.isValidatedAuth){
        swal({
          title: '   ',
          icon: 'error',
          button: ''
        })
      }
      else {
        signupForm.value.validate((valid) => {
          if (valid) {
            //  , ,   
            loading.value = true
            const arr = state.form.birthDate.split("-")
            const year = arr[0]
            const month = arr[1]
            const day = arr[2]
            // store.dispatch('root/requestRegister', { userId: state.form.id, password: state.form.password, name: state.form.name, year: year, month: month, day: day, nickName: state.form.nickname })
            store.dispatch('root/requestRegister', { userId: state.form.id, password: state.form.password, name: state.form.name, birthDay: state.form.birthDate, nickName: state.form.nickname })
            .then(function (result) {
              console.log(' ')
              // console.log('   ')
              setTimeout(() => {
                loading.value = false
                swal({
                  title: "    .",
                  text: "  .",
                  icon: "success",
                  button: "",
                });
              }, 500)

              // router.push({
              //   name: 'schedule',
              // })

              handleClose()
            })
            .catch(function (error) {
              loading.value = false
              console.log(' ')
            })
          } else {
            swal({
              title: " ",
              text: "  .",
              icon: "error",
              button: "",
            })
          }
        });
      }
    }

    const handleClose = function () {
      state.form.id = ''
      state.form.password = ''
      state.form.passwordConfirm = ''
      state.form.name = ''
      state.form.nickname = ''
      state.form.agreement = false
      state.form.birthDate = ''
      state.form.isValidatedId = false
      state.form.isValidatedAuth = false
      state.form.isPossibleId = false
      state.form.isPossibleAuth = false
      emit('closeSignupDialog')
    }

    const clickCustomercenter = function () {
      store.commit('root/setMenuActive', 2)
      handleClose()
    }

    const checkDup = function () {
      if (!state.form.isPossibleId) {
        swal({
            title: " .",
            text: "5 ~ 16    ,   .",
            icon: "error",
            button: "",
          });
        state.form.isPossibleId = false
      } else {

        store.dispatch('root/checkDupId', { userId: state.form.id })
        .then(res => {
          state.form.isValidatedId = true
          swal({
              title: " .",
              icon: "success",
              button: "",
            });
            document.getElementById("auth-number").focus()
            sendAuthEmail();
        })
        .catch(err => {
          swal({
              title: "  .",
              icon: "error",
              button: "",
            });
        })
      }
    }

    const sendAuthEmail = function () {
        store.dispatch('root/getAuthNumber', { userEmail: state.form.id })
        .then(res => {
          state.form.isValidatedId = true
          state.auth = res.data;

          swal({
              title: "  .",
              icon: "success",
              button: "",
            });
        })
        .catch(err => {
          swal({
              title: "  .",
              icon: "error",
              button: "",
            });
        })

    }

    return { signupForm, state, authForm, clickAuthup, clickSignup, handleClose, clickCustomercenter, checkDup, loading ,sendAuthEmail}
  }
}
</script>
