<template>
  <div>
  <el-dialog custom-class="login-dialog" v-model="open" @close="handleClose" :modal="state.form.modalStatus">
    <!-- header -->
    <template #title>
      <span>
          
      </span>
    </template>

    <!-- cpntent -->
    <el-form :model="state.form" :rules="state.rules" ref="loginForm" :label-position="state.form.align">
      <el-form-item prop="id" label="" :label-width="state.formLabelWidth" >
        <el-input v-model="state.form.id" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="password" label="" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.password" autocomplete="off" show-password @keyup.enter="clickLogin"></el-input>
      </el-form-item>
    </el-form>

    <!-- footer -->
    <template #footer>
      <div class="dialog-footer">
        <el-button type="primary" @click="clickLogin" v-loading.fullscreen="loading"></el-button>
        <div class="easy-login">
          <div class="login-title"></div>
          <div class="login-btn-wrap">
            <button @click="handleClickSignIn" class='easy-custom-btn google-btn'></button>
            <button @click="handleClickSignIn" class='easy-custom-btn naver-btn'></button>
            <button @click="handleClickSignIn" class='easy-custom-btn kakao-btn'></button>
          </div>
        </div>
      </div>
    </template>
  </el-dialog>

  </div>
</template>
<style>
.login-dialog {
  width: 400px !important;
  height: 400px;
}
.login-dialog .el-dialog__headerbtn {
  float: right;
}
.login-dialog .el-form-item__content {
  margin-left: 0 !important;
  width: 220px;
  display: inline-block;
}
.login-dialog .el-form-item {
  margin-bottom: 20px;
}
.login-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.login-dialog .el-input__suffix {
  display: none;
}
.login-dialog .el-dialog__footer {
  width: 100%;
  padding-top: 0;
  display: inline-block;
}
.login-dialog .dialog-footer{
  text-align: center;
}
.login-dialog .dialog-footer .el-button {
  width: 120px;
}
.easy-login{
  font-size: 11px;
  margin-top: 5%;
}

.easy-login .login-title{
  font-size: 0.95rem;
}

.easy-custom-btn{
  width: 40px !important;
  height: 35px !important;
  margin: auto;
  margin-top: 15px;
  background-size: contain;
  background-repeat: no-repeat;
  border: 0px;
  text-align: center;
  background-color: #fff;
}

.easy-custom-btn:nth-child(2){
  margin: 0 5px;
}

.google-btn{
  background-image: url('../../../assets/images/googleicon.png');
}

.naver-btn{
  background-image: url('../../../assets/images/navericon.png');
}

.kakao-btn{
  background-image: url('../../../assets/images/kakaoicon.png');
}

</style>
<script>
import { reactive, computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default {
  name: 'login-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },
  data(){
    return {
      user: '',
      name:'',
      email:'',
      imageurl:'',
      id:'',
      idToken:'',
    }
  },
methods: {
    async handleClickSignIn(){
      try {
        this.loading = true
        const googleUser = await this.$gAuth.signIn();
        if (!googleUser) {
          this.loading = false
          return null;
        }
        const authuser = await this.$gAuth.instance;
        console.log('authuser', authuser);
        console.log('googleUser', googleUser);
        this.user = googleUser.getBasicProfile().getEmail();
        this.name = googleUser.getBasicProfile().getName();
        this.imageurl = googleUser.getBasicProfile().getImageUrl();
        this.id = googleUser.getBasicProfile().getId();
        this.idToken = this.$gAuth.instance.currentUser.get().getAuthResponse().id_token;
        this.tokenVerify()
      } catch (error) {
        //on fail do something
        console.error(error);
        this.loading = false
        return null;
      }
    },
    async handleClickGetAuthCode(){
      try {
        const authCode = await this.$gAuth.getAuthCode();
        console.log('authCode', authCode);
      } catch(error) {
        //on fail do something
        console.error(error);
        return null;
      }
    },
    async handleClickSignOut() {
      try {
        await this.$gAuth.signOut();
        console.log('isAuthorized', this.Vue3GoogleOauth.isAuthorized);
        this.user = '';
      } catch (error) {
        console.error(error);
      }
    },
    handleClickDisconnect() {
      window.location.href = `https://www.google.com/accounts/Logout?continue=https://appengine.google.com/_ah/logout?continue=${window.location.href}`;
      console.log('')
    },

  },
  setup(props, { emit }) {
    const store = useStore()
    //      -  . <return>
    const loginForm = ref(null)
    const router = useRouter()
    // const dialogVisible = ref(false)
    const loading = ref(false) //    . true   .

    /*
      // Element UI Validator
      // rules    form      
      //
    */

    console.log('   in logindialog setup')
    console.log(props.open)


    const state = reactive({
      form: {
        id: '',
        password: '',
        align: 'left',
        modalStatus: true
      },
      rules: {
        id: [
          { required: true, message: ' .', trigger: 'blur' }
        ],
        password: [
          { required: true, message: ' .', trigger: 'blur' }
        ]
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: '120px'
    })

    onMounted(() => {
      // console.log(loginForm.value)
    })


    const clickLogin = function () {
      //    validate      ,  API    
      loginForm.value.validate((valid) => {
        if (valid) {
          console.log('submit')
          loading.value = true
          const userId = state.form.id
          const userPw = state.form.password
          store.dispatch('root/requestLogin', { userId: userId, password: userPw })
          .then(function (result) {
            router.push({ name: 'schedule' })
            store.commit('root/setMenuActive', 0)

            localStorage.setItem('JWT', result.data.accessToken)
            store.commit('root/setJWTToken', result.data.accessToken)

            //      
            const token = store.getters['root/getJWTToken']
            store.dispatch('root/getTeamInfo', token)
            .then(function (result) {
              store.commit('root/setUserTeam', result.data)
              console.log('  ')
              console.log(result.data)
            })

            store.dispatch('root/getProfile', token)
              .then(function (result) {
                store.commit('root/setNickName', result.data.nickName)
              })
              .catch(function (error) {
                console.log(error)
              })

            handleClose() //   
          })
          //        
          //            .
          .then(() => {
            setTimeout(() => {
              loading.value = false
            }, 500)
          })
          .catch(function (err) {
            setTimeout(() => {
              loading.value = false
              swal({
                title: "   .",
                text: "   .",
                icon: "error",
                button: "",
              });
            }, 500)
          })
        }
      })
    }

    const handleClose = function () {
      state.form.id = ''
      state.form.password = ''
      console.log('  ')
      console.log(state.dialogVisible)
      emit('closeLoginDialog')
    }
    const tokenVerify = function(){

      const params = new URLSearchParams();
      params.append('idToken', this.idToken);
      console.log(this.idToken);

      store.dispatch('root/verifyToken', {accessToken:this.idToken})
          .then(function (result) {
            router.push({ name: 'schedule' })
            store.commit('root/setMenuActive', 0)

            localStorage.setItem('JWT', result.data.accessToken)
            store.commit('root/setJWTToken', result.data.accessToken)

            //      
            const token = store.getters['root/getJWTToken']
            store.dispatch('root/getTeamInfo', token)
            .then(function (result) {
              store.commit('root/setUserTeam', result.data)
              console.log('  ')
              console.log(result.data);
            })

            store.dispatch('root/getProfile', token)
              .then(function (result) {
                store.commit('root/setNickName', result.data.nickName)
              })
              .catch(function (error) {
                console.log(error)
              })

            handleClose()
            // store.dispatch('root/getProfile', token)
            // .then(function (result) {
            //   console.log(result.data.birthDay)
            //   if (result.data.birthDay == '' || result.data.birthDay == null || result.data.birthDay == undefined) {
            //     console.log('')
            //     router.push({
            //     name: 'Profile',
            //   })
            //   }
            // })


          })
          //        
          //            .
          .then(() => {
            store.dispatch('root/getProfile', token)
            .then(function (result) {
              console.log(result.data.birthDay)
              if (result.data.birthDay == '' || result.data.birthDay == null || result.data.birthDay == undefined) {
                console.log('')
                router.push({
                name: 'Profile',
              })
              }
            })
            setTimeout(() => {
              loading.value = false

            }, 1000)
          })
          .catch(function (err) {
            console.log(err);
            loading.value = false
          })
    }
    return { loginForm, state, clickLogin, handleClose, loading,tokenVerify }
  }
}
</script>

