<template>
  <div v-if="info">
    <el-dialog custom-class="before-dialog" v-model="state.dialogVisible" title=" " width="30%">
      <div class="card text-dark" style="margin-bottom: -20px;">
        <div v-if="teamP">
          <img :src=teamP class="card-img" onerror="src='https://cdn.pixabay.com/photo/2017/04/10/22/28/residence-2219972_960_720.jpg'" alt="https://i6a403.p.ssafy.io/img/agit_logo.e339fd5c.png" style="opacity: 0.5;">
          <div class="card-img-overlay" style="color: #464646; opacity: 0.7;">
            <h3 class="card-title">: {{ info.teamName }}</h3>
            <br>
            <h5 class="card-text"> : {{ info.eventTitle }}</h5>
            <h5 class="card-text"> : {{ info.eventContent }}</h5>
            <br>
            <p class="card-text"> : {{ info.endDate }}</p>
          </div>
        </div>
        </div>

      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="handleClose()"
            >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
  <div v-else-if="info == undefined">
    <el-dialog custom-class="before-dialog" v-model="state.dialogVisible" title=" " width="30%" style="margin: 50%">
      <p>
        .
      </p>
      <br>
      <p>       <i class="em em-closed_book" aria-role="presentation" aria-label="CLOSED BOOK" style="margin-bottom:4px"></i></p>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="primary" @click="handleClose()"
            >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>


</template>

<script>
import { reactive, computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'


export default {
  name: 'before-dialog',
  props: {
    open: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
      required: true,
    },
    teamP: {
      type: String,
    }
  },
  setup(props, {emit}) {
    const store = useStore()
    const router = useRouter()
    const state = reactive({
      dialogVisible: computed(() => props.open),
      teamP : props.teamP
    })

  // console.log(props.info)
  // console.log('')


    const handleClose = function () {
      emit('closeBeforeDialog')
    }

    return { state, store, router, handleClose }
  }
}
</script>

<style>
  .before-dialog{
    top: -80px;
  }
</style>
