<template>
  <el-dialog
    custom-class="create-schedule-dialog"
    v-model="state.dialogVisible"
    @close="handleClose"
    width="30%"
    :destroy-on-close="true"
  >
    <!-- header -->
    <template #title>
      <span>
           
      </span>
    </template>

    <!-- content -->
    <div>
      <div>
         .
      </div>
      <div>
        <el-form :model="state.form" :rules="state.rules" ref="createScheduleForm" :label-position="state.form.align">
          <!-- <el-form-item prop="startdate" label="">
            <el-date-picker
              style="width: 100%;"
              v-model="registerDate"
              type="date"
              value-format="YYYY-MM-DD"
              placeholder=" ."
            >
            </el-date-picker>
          </el-form-item> -->
          <el-form-item prop="enddate" label="">
            <el-date-picker
              style="width: 100%;"
              v-model="state.form.enddate"
              type="date"
              value-format="YYYY-MM-DD"
              placeholder=" ."
              disabled
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="title" label="">
            <el-input
              v-model="state.form.title"
              maxlength="20"
              placeholder="  ."
              show-word-limit
              rows="1"
              type="textarea"
              resize="none"
            >
            </el-input>
          </el-form-item>
          <el-form-item prop="content" label="">
            <el-input
              v-model="state.form.content"
              maxlength="100"
              placeholder="  ."
              show-word-limit
              rows="5"
              type="textarea"
              resize="none"
            >
            </el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- footer -->
    <template #footer>
      <span>
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="createSchedule"></el-button>
      </span>
      <!-- <span>
        {{ info.teamId }}
      </span> -->
    </template>
  </el-dialog>
</template>

<script>
import { reactive, computed, ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default {
  name: 'create-schedule-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
      required: true,
    },
    registerDate: {
      type: String,
      default: '1970-01-01'
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    const router = useRouter()
    const createScheduleForm = ref(null)

    const state = reactive({
      form: {
        align: 'left',
        content: '',
        startdate: computed(() => props.registerDate),
        enddate: computed(() => props.registerDate),
        title: '',
        teamId: '',
      },
      rules: {
        startdate: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        enddate: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        title: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        content: [
          { required: true, message: '  .', trigger: 'blur'}
        ]
      },
      dialogVisible: computed(() => props.open),
    })

    const handleClose = function () {
      state.form.content = ''
      state.form.title = ''
      // state.form.enddate = ''
      emit('closeCreateScheduleDialog')
    }

    const createSchedule = function () {
      const token = store.getters['root/getJWTToken']
      const body = {
        // 'teamId': props.info.teamId,
        'eventTitle': state.form.title,
        'eventContent': state.form.content,
        'startDate': state.form.startdate,
        'endDate': state.form.enddate
      }
      const teamId = props.info.teamId
      const uploadDate = state.form.startdate.slice(0, -3)
      // const teamId = props.info.teamId
      store.dispatch('root/createSchedule',{ 'body': body, 'token': token, 'teamId': teamId})
      .then(res => {
        setTimeout(() => {
          swal({
            title: ' ',
            text: '  .',
            icon: 'success',
            button: '',
          });
        }, 500)

        // router.go(router.currentRoute)
        console.log(res)
        // console.log(swal)
        emit('createSchedule')
        handleClose()
      })
      .catch(err => {
        console.log(err)
      })

      handleClose()
    }



    return { state, handleClose, createScheduleForm, createSchedule, store, router }
  }
}
</script>

<style>
.create-schedule-dialog {
  width: 700px;
  height: 1200px;
}


</style>
