<template>
  <el-dialog
    custom-class="upload-image-dialog"
    v-model="state.dialogVisible"
    @close="handleClose"
    width="30%"
  >
    <!-- header -->
    <template #title>
      <span>
           
      </span>
    </template>

    <!-- content -->
    <div>
      <div>
         .
      </div>
      <div>
        <el-form :model="state.form" :rules="state.rules" ref="uploadImageForm" :label-position="state.form.align">
          <el-form-item prop="schedule" label="">
            <el-date-picker
              style="width: 100%;"
              v-model="registerDate"
              type="date"
              value-format="YYYY-MM-DD"
              placeholder=" ."
              disabled
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="content" label="">
          </el-form-item>
          <input
            id="uploadImage"
            type="file"
            accept="image/*"
            multiple
            @change="selectedImage"
            ref="inputImage"
          />
        </el-form>
      </div>
      <div v-for="img in state.form.images" width=150 :key="img.name">
        <img :src="img.preview" alt="" width=100>
      </div>
    </div>

    <!-- footer -->
    <template #footer>
      <span>
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="uploadImage"></el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { reactive, computed, ref } from 'vue'
import { useStore } from 'vuex'
export default {
  name: 'upload-image-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    },
    info:{
      type:Object,
      required:true,
    },
    registerDate: {
      type: String,
      default: '1970-01-01'
    }
  },

  setup(props, { emit }) {
    const uploadImageForm = ref(null)
    const inputImage = ref(null)
    const store = useStore();
    const state = reactive({
      form: {
        align: 'left',
        images: [],
        schedule: computed(() => props.registerDate),
      },
      rules: {
        schedule: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        content: [
          { required: true, message: '  .', trigger: 'blur'}
        ]
      },
      dialogVisible: computed(() => props.open),
      isLogin: computed(()=> store.getters['root/getJWTToken'])
    })

    const handleClose = function () {
      state.form.content = ''
      state.form.images = []
      document.getElementById('uploadImage').value = ''
      emit('closeUploadImageDialog')
    }

    const selectedImage = function () {
      console.log('')
      for (let i = 0; i < inputImage.value.files.length; i++) {
        console.log(URL.createObjectURL(inputImage.value.files[i]))
        console.log(inputImage.value);
        console.log(state.form.images)
        state.form.images.push({
          file: inputImage.value.files[i],
          preview: URL.createObjectURL(inputImage.value.files[i])
        })

      }
    }

      const uploadImage = function(){
        let formData = new FormData();
        for (let i=0; i<state.form.images.length; i++){
          formData.append('upfile', state.form.images[i].file);
        }
        formData.append('uploadDate',state.form.schedule);
        formData.append('teamId',props.info)

        store.dispatch('root/uploadImage',{ 'formData': formData, 'token': state.isLogin})
        .then(res => {
          setTimeout(() => {
                swal({
                  title: ' ',
                  text: '  .',
                  icon: 'success',
                  button: '',
                });
              }, 500)

              // router.go(router.currentRoute)
              console.log(res)
              // console.log(swal)
              emit('createImage')
              handleClose()
          })
          .catch(err => {
            console.log(err)
          })
      }

    return { state, handleClose, uploadImageForm, inputImage, selectedImage, uploadImage }
  }
  }
</script>

<style>
.upload-image-dialog {
  width: 700px;
  height: 600px;
}
img{
  float: left;
}
</style>
