// WebRtc
// 
export function sendMessage({state}, payload){
  console.log(payload)
  var messageData = {
    content: payload.msssage,
    secretName: state.secretName
  }
  state.session.signal({
    type: 'chat',
    data: JSON.stringify(messageData),
    to: [],
  })
}


