<template>
  <el-dialog custom-class="signup-dialog" v-model="state.dialogVisible" @close="handleClose" :destroy-on-close="true" :close-on-click-modal="false" :close-on-press-escape="false" top="100px">
    <!-- header -->
    <template #title>
      <span>
         
      </span>
    </template>

    <!-- content -->
    <el-form :model="state.form" status-icon :rules="state.rules" ref="signupForm" :label-position="state.form.align">
      <el-form-item prop="email" label="" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.id" autocomplete="off" style="width: 70%" :disabled="state.form.isValidatedId" id="id-input" placeholder=""></el-input>
        <el-button size="small" style="float: right; margin-top:5px;" @click="sendAuthEmail" :disabled="state.form.isValidatedId"> </el-button>
      </el-form-item>
      <el-form-item prop="authNumber" label="" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.authNumber" autocomplete="off" show-authNumber placeholder=""></el-input>
      </el-form-item>



    </el-form>

    <!-- footer -->
    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="clickAuthup" v-loading.fullscreen="loading"></el-button>
      </span>
    </template>
  </el-dialog>
</template>
<style>
.signup-dialog {
  width: 500px !important;
  height: 550px;
}
.signup-dialog .checkbox .el-form-item__content {
  float: left;
  margin-left: 3rem !important;
}
.signup-dialog .el-dialog__body {
  padding-bottom: 0px;
}
.signup-dialog .el-dialog__headerbtn {
  float: right;
}
.signup-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 300px;
  display: inline-block;
}
.signup-dialog .el-form-item {
  margin-bottom: 20px;
}
.signup-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.signup-dialog .el-input__suffix {
  display: none;
}
.signup-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.signup-dialog .dialog-footer .el-button {
  width: 120px;
}
.signup-dialog .el-checkbox a:hover {
  background-color: rgba(141, 29, 216, 0.322);
}

</style>
<script>
import { reactive, computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default {
  name: 'auth-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    //      -  . <return>
    const signupForm = ref(null)
    const router = useRouter()
    const loading = ref(false)
    /*
      // Element UI Validator
      // rules    form      
      //
    */
    const state = reactive({
      auth: {

      },
      form: {
        id: '',
        authNumber: '',
      },
      dialogVisible: computed(() => props.open),
      isAvailable: computed(function () {
        return signupForm.value.validate((valid) => valid)
      }),
      formLabelWidth: '120px',
    })

    onMounted(() => {
      // console.log(loginForm.value)
    })

    const clickAuthup = function () {
      //    validate      ,  API    
      if (!state.form.isValidatedId) {
        swal({
          title: '  .',
          icon: 'error',
          button: ''
        })
      } else {
        signupForm.value.validate((valid) => {
          if (valid) {
            //  , ,   
            loading.value = TextTrackCue
            if(state.form.authNumber==state.auth.str){
              //   
              console.log(" ");
              setTimeout(() => {
                loading.value = false
                swal({
                  title: " ",
                  text: " .",
                  icon: "success",
                  button: "",
                });
              }, 500)
            }


          } else {
            swal({
              title: " ",
              text: "  .",
              icon: "error",
              button: "",
            })
          }
        });
      }
    }

    const handleClose = function () {
      state.form.id = ''
      state.form.authNumber = ''
      emit('closeAuthDialog')
    }

    const clickCustomercenter = function () {
      store.commit('root/setMenuActive', 2)
      handleClose()
    }

    const sendAuthEmail = function () {
        store.dispatch('root/getAuthNumber', { userEmail: state.form.id })
        .then(res => {
          state.form.isValidatedId = true
          state.auth = res.data;
          swal({
              title: "  .",
              icon: "success",
              button: "",
            });
        })
        .catch(err => {
          swal({
              title: "  .",
              icon: "error",
              button: "",
            });
        })

    }

    return { signupForm, state, clickAuthup, handleClose, clickCustomercenter, sendAuthEmail, loading }
  }
}
</script>
