<template>
  <el-dialog
    custom-class="create-article-dialog"
    v-model="state.dialogVisible"
    @close="handleClose"
    width="30%"
    :destroy-on-close="true"
  >
    <!-- header -->
    <template #title>
      <span>
            
      </span>
    </template>

    <!-- content -->
    <div>
      <div>
         .
      </div>
      <div>
        <el-form :model="article" :rules="state.rules" ref="createArticleForm" :label-position="state.form.align">
          <el-form-item prop="uploadDate" label="">
            <el-date-picker
              style="width: 100%;"
              v-model="article.uploadDate"
              type="date"
              v-if="isUpdated"
              value-format="YYYY-MM-DD"
              placeholder=" ."
              disabled
            >
            </el-date-picker>
            <el-date-picker
              style="width: 100%;"
              v-model="state.form.uploadDate"
              v-else
              type="date"
              value-format="YYYY-MM-DD"
              placeholder=" ."
              disabled
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="title" label="" :label-width="state.formLabelWidth" >
            <el-input v-model="article.title" autocomplete="off" placeholder=" ."></el-input>
          </el-form-item>
          <el-form-item prop="content" label="" :label-width="state.formLabelWidth">
            <el-input
              v-model="article.content"
              maxlength="500"
              placeholder=" ."
              show-word-limit
              rows="10"
              type="textarea"
              resize="none"
            >
            </el-input>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- footer -->
    <template #footer>
      <span v-if="isUpdated">
        <el-button @click="handleClose"></el-button>
        <el-button type="info" @click="updateArticle"> </el-button>
      </span>
      <span v-else>
        <el-button @click="handleClose"></el-button>
        <el-button type="primary" @click="createArticle"> </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { reactive, computed, ref } from 'vue'
import { useStore } from 'vuex'
import jwt_decode from 'jwt-decode'

export default {
  name: 'create-article-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    },
    info: {
      type: Object,
    },
    isUpdated : {
      type: Boolean,
      default: false
    },
    registerDate: {
      type: String,
      default: '1970-01-01'
    },
    article: {
      type: Object,
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    const createArticleForm = ref(null)

    const state = reactive({
      form: {
        align: 'left',
        content: '',
        title: '',
        uploadDate: computed(() => props.registerDate),
      },
      rules: {
        uploadDate: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        title: [
          { required: true, message: '  .', trigger: 'blur'}
        ],
        content: [
          { required: true, message: '  .', trigger: 'blur'}
        ]
      },
      dialogVisible: computed(() => props.open),
    })

    const handleClose = function () {
      emit('closeCreateArticleDialog')
      props.article = {
        content: '',
        createdTime: '',
        id: '',
        index: '',
        nickName: '',
        teamName: '',
        title: '',
        updatedDate: '',
        uploadDate: '',
        writer: '',
      }
    }

    const createArticle = function () {
      const token = store.getters['root/getJWTToken']
      const writer = jwt_decode(token).sub
      const teamName = props.info.teamName
      const title = props.article.title
      const content = props.article.content
      const uploadDate = state.form.uploadDate
      const nickName = store.state.root.nickName
      const payload = {
        'body': {
          'writer': writer,
          'teamName': teamName,
          'title': title,
          'content': content,
          'uploadDate': uploadDate,
          'nickName': nickName
        }
      }
      // console.log(' ')
      // console.log(payload)

      store.dispatch('root/addArticle', payload)
      .then(function (result) {
        console.log('  ')
        // console.log(uploadDate.slice(0, -3))
        emit('createArticle')
        swal({
          title: " ",
          text: " .",
          icon: "success",
          button: "",
        })
      })
      .catch(function (error) {
        console.log('  ')
      })
      handleClose()
    }

    const updateArticle = function () {
      const writer = props.article.writer
      const teamName = props.article.teamName
      const title = props.article.title
      const content = props.article.content
      const uploadDate = props.article.uploadDate
      const nickName = props.article.nickName
      const id = props.article.id
      const payload = {
        'id' : id,
        'body': {
          'writer': writer,
          'teamName': teamName,
          'title': title,
          'content': content,
          'uploadDate': uploadDate,
          'nickName': nickName
        }
      }

      store.dispatch('root/updateArticle', payload)
      .then(function (result) {
        console.log('  ')
        emit('updateArticle', uploadDate)
        swal({
          title: " ",
          text: " .",
          icon: "success",
          button: "",
        })
      })
      .catch(function (error) {
        console.log('  ')
      })

      handleClose()
    }

    return { state, handleClose, createArticleForm, createArticle, updateArticle }
  }
}
</script>

<style>
.create-schedule-dialog {
  width: 700px;
  height: 600px;
}
</style>
